import { useEffect, useState } from 'react';
import { Container, Typography, Button, Stack, Paper, Grid, TextField, Dialog, DialogActions, DialogContent, DialogTitle } from '@mui/material';
import { useNavigate } from 'react-router-dom';

const Home: React.FC = () => {
  const navigate = useNavigate();
  const [hasResults, setHasResults] = useState(false); //   
  const [password, setPassword] = useState('');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isPasswordDialogOpen, setIsPasswordDialogOpen] = useState(!localStorage.getItem('isAuthenticated'));

  //     
  useEffect(() => {
    const storedImages = localStorage.getItem('imageRatings');
    if (storedImages) {
      const parsedImages = JSON.parse(storedImages);
      const hasRatings = parsedImages.some((image: { ratings?: number[] }) =>
        image.ratings && image.ratings.length > 0
      );
      setHasResults(hasRatings);
    }
  }, []);

  const handlePasswordSubmit = () => {
    if (password === '0412') {
      setIsAuthenticated(true);
      setIsPasswordDialogOpen(false);
      const expiration = new Date();
      expiration.setHours(23, 59, 59, 999); //   
      localStorage.setItem('isAuthenticated', JSON.stringify({ value: true, expires: expiration.getTime() }));
    } else {
      alert(' .');
    }
  };

  useEffect(() => {
    const authData = localStorage.getItem('isAuthenticated');
    if (authData) {
      const parsedAuthData = JSON.parse(authData);
      if (new Date().getTime() > parsedAuthData.expires) {
        localStorage.removeItem('isAuthenticated');
        setIsAuthenticated(false);
        setIsPasswordDialogOpen(true);
      } else {
        setIsAuthenticated(true);
      }
    }
  }, []);

  return (
    <Container
      style={{
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '10px',
      }}
    >
      <Dialog open={isPasswordDialogOpen} disableEscapeKeyDown>
        <DialogTitle>D-Day?!</DialogTitle>
        <DialogContent>
          <TextField
            type="password"
            label=""
            variant="outlined"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            fullWidth
            margin="normal"
          />
        </DialogContent>
        <DialogActions>
          <Button
            variant="contained"
            color="primary"
            onClick={handlePasswordSubmit}
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      {isAuthenticated && (
        <Grid container spacing={2} justifyContent="center">
          <Grid item xs={12} sm={8} md={6}>
            <Paper
              elevation={3}
              style={{
                padding: '30px',
                borderRadius: '12px',
                textAlign: 'center',
                backgroundColor: '#ffffff',
              }}
            >
              <Typography variant="h3" gutterBottom>
                 
              </Typography>
              <Typography variant="body1" gutterBottom>
                 1~5 !
              </Typography>
              <Stack spacing={2} direction="column" alignItems="center" marginTop={2}>
                <Button
                  variant="contained"
                  color="primary"
                  size="large"
                  onClick={() => navigate('/select-category')}
                  style={{ width: '100%' }}
                >
                  
                </Button>
                <Button
                  variant="outlined"
                  color="secondary"
                  size="large"
                  onClick={() => navigate('/results')}
                  style={{ width: '100%' }}
                  disabled={!hasResults} //    
                >
                   
                </Button>
              </Stack>
            </Paper>
          </Grid>
        </Grid>
      )}
    </Container>
  );
};

export default Home;
