import { ImageRatings } from "../types";

// utils/loadImages.ts
export const loadImagesByCategory = async (category: string) => {
    const response = await fetch('/photo-rating-deploy/imageList.json');
    const images = await response.json();
    return images.filter((image: { category: string }) => image.category === category);
  };
  
  export const allLoadImages = async () => {
    const response = await fetch('/photo-rating-deploy/imageList.json');
    const images = await response.json();
    return images as ImageRatings; //   
  };