import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { Image, ImageRatings } from '../types';

import ImageRating from '../components/ImageRating';
import { Container, Typography, Paper, CircularProgress, Stack, Button } from '@mui/material';
import { loadImagesByCategory } from '../utils/loadImages'; //    
import { calculateImageAverage } from '../utils/ratingUtils';

//    
export const allLoadImages = async () => {
  const response = await fetch('/photo-rating-deploy/imageList.json');
  const images = await response.json();
  return images as ImageRatings; //   
};

const RatingPage: React.FC = () => {
  const [images, setImages] = useState<Image[]>([]); //   
  const [allImages, setAllImages] = useState<Image[]>([]); //   
  const [currentIndex, setCurrentIndex] = useState(0); //   
  const navigate = useNavigate();
  const location = useLocation();
  const { category, minRating } = location.state as { category: string, minRating: number };

  useEffect(() => {
    const fetchImages = async () => {
      const storedRatings = localStorage.getItem('imageRatings');
      let data: Image[] = [];

      if (storedRatings) {
        const ratingsData : Image[] = JSON.parse(storedRatings);
        //      
        data = ratingsData.filter((data) => data.category as unknown === category && (data.ratings && calculateImageAverage(data.ratings) >= minRating || data.ratings === undefined)) || []; //   
      }

      if (data.length === 0) {
        //      
        data = await loadImagesByCategory(category);
      }

      setImages(data);
    };

    const fetchAllImages = () => {
      const storedRatings = localStorage.getItem('imageRatings');
      const allData : Image[] = storedRatings ? JSON.parse(storedRatings) : []; //    

      if (allData.length === 0) {
        //      
        allLoadImages().then((data) => {
          setAllImages(data); //    
        });
      } else {
        setAllImages(allData.filter((data) =>(data.ratings && calculateImageAverage(data.ratings) >= minRating || data.ratings === undefined))); //    
      }
    };

    fetchImages();
    fetchAllImages(); //   
  }, [category]);

  const handleRating = (rating: number) => {
    const updatedImages = [...images];
    const currentImage = updatedImages[currentIndex];

    //      
    const newRatings = [
      ...(currentImage.ratings || []), //   
      rating, //   
    ]; 

    currentImage.ratings = newRatings; //   

    setImages(updatedImages); //  

    //     
    const updatedAllImages = allImages.map((image) => {
      const updatedImage = updatedImages.find((img) => img.id === image.id);
      return updatedImage ? updatedImage : image; //    ,    
    });

    localStorage.setItem('imageRatings', JSON.stringify(updatedAllImages)); //    

    if (currentIndex + 1 < images.length) {
      setCurrentIndex(currentIndex + 1);
    } else {
      navigate('/results');
    }
  };

  if (images.length === 0) return <CircularProgress />;

  return (
    <Container maxWidth="lg" style={{ marginTop: '20px', padding: '3px' }}>
      <Paper elevation={3} style={{ padding: '3px', borderRadius: '12px' }}>
        <Typography variant="h5" gutterBottom>
           {currentIndex + 1} / {images.length}
        </Typography>
        <ImageRating image={images[currentIndex]} onRate={handleRating} />
        <Stack direction="row" spacing={2} justifyContent="center" marginTop={2}>
          <Button variant="outlined" color="secondary" onClick={() => navigate('/')}>
            
          </Button>
        </Stack>
      </Paper>
    </Container>
  );
};

export default RatingPage;
