import { useNavigate } from 'react-router-dom';
import { Container, Typography, Button, Stack, Paper, FormControl, InputLabel, Select, MenuItem, SelectChangeEvent } from '@mui/material';
import { Categories, CategoryType } from '../types';
import { ChangeEvent, useState } from 'react';


const CategorySelectPage: React.FC = () => {
  const navigate = useNavigate();
  const [minRating, setMinRating] = useState<number>(1); //    

  const handleCategorySelect = (category: CategoryType) => {
    navigate('/rate', { state: { category, minRating } });
  };

  const handleRatingChange = (event: SelectChangeEvent<number>) => {
    setMinRating(event.target.value as number);
  };

  return (
    <Container
      maxWidth="sm"
      style={{ height: '100vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}
    >
      <Paper elevation={3} style={{ padding: '3px', borderRadius: '12px', textAlign: 'center' }}>
        <Typography variant="h4" gutterBottom>
          / 
        </Typography>

        <FormControl fullWidth style={{ marginBottom: '20px', marginTop: '20px' }}>
          <InputLabel> </InputLabel>
          <Select value={minRating} onChange={handleRatingChange}>
            <MenuItem value={1}>1 </MenuItem>
            <MenuItem value={2}>2 </MenuItem>
            <MenuItem value={3}>3 </MenuItem>
            <MenuItem value={4}>4 </MenuItem>
            <MenuItem value={5}>5</MenuItem>
          </Select>
        </FormControl>

        <Stack spacing={2} marginTop={2}>
          {Object.values(Categories).map((category) => (
            <Button
              key={category}
              variant="contained"
              onClick={() => handleCategorySelect(category as unknown as CategoryType)}
            >
              {category}
            </Button>
          ))}
        </Stack>
      </Paper>
    </Container>
  );
};

export default CategorySelectPage;
