import React, { useState } from 'react';
import { Image } from '../types';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Navigation } from 'swiper/modules';
import { Typography, Card, CardContent, CardMedia, Button, Box } from '@mui/material';
import RotateLeftIcon from '@mui/icons-material/RotateLeft';
import 'swiper/css'; 
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { calculateImageAverage } from '../utils/ratingUtils';

interface ResultsProps {
  images: Image[];
}

// test

const Results: React.FC<ResultsProps> = ({ images }) => {
  const [rotation, setRotation] = useState<number[]>(images.map(() => 0)); //    
  const [currentIndex, setCurrentIndex] = useState<number>(0); //    (0 )

  const rotateImage = (index: number) => {
    setRotation((prevRotation) => {
      const newRotation = [...prevRotation];
      newRotation[index] = (newRotation[index] + 90) % 360; // 90 
      return newRotation;
    });
  };

  const handleSlideChange = (swiper: any) => {
    setCurrentIndex(swiper.realIndex); //    
  }; 

  return (
    <Box>
      {/*    /     */}
      {images.length > 0 && (
        <Typography variant="subtitle1" textAlign="center" marginBottom={2}>
          {currentIndex + 1} / {images.length}  
        </Typography>
      )}

      <Swiper
        modules={[Pagination, Navigation]}
        spaceBetween={10}
        slidesPerView={1}
        navigation
        pagination={{ clickable: true, renderBullet: () => '' }} //   
        style={{ width: '100%', margin: '0 auto' }}
        onSlideChange={handleSlideChange} //    
      >
        {images.map((image, index) => (
          <SwiperSlide key={image.id}>
            <Card style={{ margin: '0 auto' }}>
              <Box 
                sx={{
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center',
                  overflow: 'visible', //     
                  position: 'relative', //     
                  marginBottom: '10px',
                }}
              > 
                <div
                  style={{
                    transform: `rotate(${rotation[index]}deg)`, //  
                    transition: 'transform 0.3s ease', //   
                  }}
                > 
                  <CardMedia
                    component="img"
                    image={image.path}
                    alt={` ${image.id}`}
                    style={{ objectFit: 'contain', width: '100%', height: 'auto' }}
                  />
                </div>
                <Box position="absolute" top={8} right={8}>
                  <Button
                    variant="contained"
                    color="primary"
                    startIcon={<RotateLeftIcon />}
                    onClick={() => rotateImage(index)}
                  >
                    
                  </Button>
                </Box>
              </Box>
              <CardContent>
                <Typography variant="subtitle1" textAlign={"center"} gutterBottom>
                  : {image.fileName}
                </Typography>
                <Typography variant="h6" textAlign="center">
                   : {calculateImageAverage(image.ratings || [])}
                </Typography>
              </CardContent>
            </Card>
          </SwiperSlide>
        ))}
      </Swiper>
    </Box>
  );
};

export default Results;
