import React, { useState, useEffect } from 'react';
import { Image } from '../types';
import { Stack, IconButton, Card, CardMedia, Box, Button, Typography } from '@mui/material';
import StarIcon from '@mui/icons-material/Star';
import RotateLeftIcon from '@mui/icons-material/RotateLeft';

interface ImageRatingProps {
  image: Image;
  onRate: (rating: number) => void;
}

const ImageRating: React.FC<ImageRatingProps> = ({ image, onRate }) => {
  const [hoverRating, setHoverRating] = useState<number | null>(null);
  const [selectedRating, setSelectedRating] = useState<number | null>(null);
  const [rotation, setRotation] = useState<number>(0);

  useEffect(() => {
    //        .
    if (selectedRating !== null) {
      const timer = setTimeout(() => {
        setSelectedRating(null);
      }, 200); // 0.2    .

      return () => clearTimeout(timer);
    }

    setHoverRating(null);
    setRotation(0); //      .
  }, [image]);

  const handleMouseEnter = (rating: number) => setHoverRating(rating);
  const handleMouseLeave = () => setHoverRating(null);
  const handleTouchStart = (rating: number) => setHoverRating(rating); //    
  const handleClick = (rating: number) => {
    setSelectedRating(rating);
    onRate(rating);
  };

  const rotateImage = () => {
    setRotation((prevRotation) => (prevRotation + 90) % 360);
  };

  return (
    <Card>
      <Box position="relative">
        <CardMedia
          component="img"
          image={`${image.path}`}
          alt={` ${image.id}`}
          style={{
            objectFit: 'cover',
            transform: `rotate(${rotation}deg)`,
            transition: 'transform 0.3s ease',
            width: '100%',
          }}
        />
        <Box position="absolute" top={8} right={8}>
          <Button
            variant="contained"
            color="primary"
            startIcon={<RotateLeftIcon />}
            onClick={rotateImage}
          >
            
          </Button>
        </Box>
      </Box>

      <Box padding={2}>
        <Typography variant="h6" align="center" gutterBottom>
          {image.fileName}
        </Typography>
        <Stack direction="row" spacing={1} justifyContent="center">
          {[1, 2, 3, 4, 5].map((star) => (
            <IconButton
              key={star}
              onMouseEnter={() => handleMouseEnter(star)}
              onMouseLeave={handleMouseLeave}
              onTouchStart={() => handleTouchStart(star)} //    
              onClick={() => handleClick(star)}
            >
              <StarIcon
                style={{
                  color: star <= (hoverRating ?? selectedRating ?? 0) ? '#FFD700' : '#E0E0E0',
                  transition: 'color 0.2s ease',
                }}
              />
            </IconButton>
          ))}
        </Stack>
      </Box>
    </Card>
  );
};

export default ImageRating;
